window.showResults =  function(val,event) {
    val = val.toLowerCase();
    let res = '';
    let list = '';
    let ul_html = '';

    if (val == '') 
      return;
    else if (val.length < 3)
        return;
        
    fetch('https://api.teleport.org/api/cities/?search=' + val).then(
     function (response) {
        return response.json();
     }).then(function (data) {
        data = data["_embedded"]["city:search-results"]
        for(let i=0; i<data.length; i++)
        {
            if (i > 4)
                continue;

            list += '<li>' + data[i]["matching_full_name"] + '</li>';
        }
        if(event.target.id == 'from')
            res = document.getElementById('result-from')
        else if(event.target.id == 'to')
            res = document.getElementById("result-to"); 
        else
          return;
        
        if (res.style.display === "none") 
            res.style.display = "block";

        ul_html = '<ul id="city-list">' + list + '</ul>';
        res.innerHTML = ul_html
        var children = res.children;
        children[0].onclick = (e) => {
            e.preventDefault()
            if(res.id == 'result-from')
                document.getElementById("from").value = e.target.innerHTML
            else if(res.id == 'result-to')
                document.getElementById("to").value = e.target.innerHTML

            res.style.display = "none"
        }
       return true;
     }).catch(function (err) {
       console.warn('Something went wrong.', err);
       return false;
     });
}

// Get the modal
const modal = document.getElementById("requestModal");

// Get the button that opens the modal
const modal_btn = document.getElementById("submit-req-btn");

// Get the button that close the modal (return btn)
const returnBtn = document.getElementById("btn-close-modal");

// Get the <span> element that closes the modal
const span = document.getElementsByClassName("close")[0];

returnBtn.onclick = function() {
  modal.style.display = "none";
}

// When the user clicks on <span> (x), close the modal
span.onclick = function() {
  modal.style.display = "none";
}

// When the user clicks anywhere outside of the modal, close it
window.onclick = function(e) {
  if (e.target == modal) {
    modal.style.display = "none";
  }
}
const phoneInputField = document.getElementById("phone-number");
const phoneInput = intlTelInput(phoneInputField, {
    placeholderNumberType: true,
    nationalMode: true,
    utilsScript: "https://cdnjs.cloudflare.com/ajax/libs/intl-tel-input/17.0.8/js/utils.js"
});


window.handleSubmit = function(event) {
    event.preventDefault();
    var myForm = event.target;
    var formatted_phone =  phoneInput.getNumber();
    // console.log(formatted_phone)
    var formData = new FormData(myForm);
    formData.set('phone-number',formatted_phone);

    if(!myForm.checkValidity()){
        return;
    }
    fetch("/", {
        method: "POST",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        body: new URLSearchParams(formData).toString(),
    })
    .catch((error) => alert(error));
    modal_btn.addEventListener("click",modal.style.display = "block")
};
