// Get the form
const form = document.getElementById('submit-form');

// Get the hr
const hrForm = document.getElementById('submit-form-hr');

// Get button that opens this form
const reqBtn = document.getElementById('btn-send-request');

//  On click display them
reqBtn.onclick = () => {
    hrForm.style.display = 'block';
    form.style.display = 'block';
}

export { form, hrForm, reqBtn }